//font

@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,200;6..12,300;6..12,400;6..12,500;6..12,600;6..12,700;6..12,800;6..12,900;6..12,1000&display=swap");

/* //colors variety import from  separate color stylesheet
@import "/css/ColorsVariety.css";
 */
$white: #f2f2f2;
$red-500: #cd3737;
$red-700: #b61a1a;
$grey-500: #dcdcdc;
$grey-600: #8d8c8c;
$grey-700: #3e3c3c;
$black-900: #120f10;
$black-700: #1c1b1b;
$black-600: #120f10;
$eastern-blue-500: #26acac;
$amulet-green-400: #85ab7b;
$ming-blue-600: #367983;

//mini- albums shadows and more
$shadow-flashback-normal: rgba(211, 142, 122, 0.5);
$color-flashback-normal: rgba(235, 152, 120, 1);

$shadow-flashback-deluxe: rgba(210, 146, 164, 0.5);
$color-flashback-deluxe: rgba(210, 146, 164, 1);

$shadow-arena-of-autumnn: rgba(185, 169, 71, 0.5);
$color-arena-of-autumnn: rgba(185, 169, 71, 1);

$shadow-galaktkitty: rgba(50, 23, 78, 0.5);
$color-galaktkitty: rgba(175, 160, 212, 1);

$shadow-king-of-for-and-muds: rgba(181, 133, 94, 0.5);
$color-king-of-for-and-muds: rgba(181, 133, 94, 1);

//other shadows
$shadow-green: rgba(133, 171, 123, 0.4);
$shadow-black: rgba(30, 29, 29, 0.6);
$shadow-black-transparent: rgba(0, 0, 0, 0.8);

* {
    margin: 0;
    box-sizing: border-box;
}

//styling

html {
    font-family: "Nunito Sans", sans-serif;
    font-size: 16px;
    overflow-x: hidden;
    background-color: $black-900;
    color: $white;
}

#background-cover {
    position: absolute;
    width: 100vw;
    height: 40rem;
    filter: blur(1rem);
    z-index: -2;
    & img {
      position: absolute;
      width: 100%;
      height: 100%;
      filter: blur(1.5rem);
      opacity: 0.8;
      object-fit: cover;
      top: 0;
      left: 0;
    }
  }
//!for mobile menu and hide desktop menu

.top-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: $shadow-black-transparent;
    backdrop-filter: blur(0.5rem);
    width: 100%;
    height: 3.5rem;
    padding: 0 1rem;
    position: fixed;
    z-index: 2;
}
#logo,
#dropdown-menu-icon {
    display: block;
    width: 1.75rem;
    height: 1.75rem;
    font-size: 1.75rem;
    transition: ease-in-out 0.2s;
    cursor: pointer;
    &:hover {
        color: $grey-500;
    }
}
#chevron-to-show-social-menu {
    display: none;
}
.nav-desktop {
    display: none;
}

//toggle menu

.toggle-menu {
    display: none;
    position: fixed;
    width: 100%;
    height: auto;
    background-color: $black-600;
    transform: translateY(3.5rem);
    color: $grey-500;

    border-radius: 0 0 5% 5%;
    box-shadow: 1px 3px 15px $shadow-black-transparent;
    opacity: 0;
}

.toggle-menu.active {
    transition: 0.2s ease-in-out;
    opacity: 1;
    display: block;
    z-index: 2;
}

.all-inside {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    padding: 1rem;
}

.toggle_menu-a {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem 2rem;
    font-size: 1.2rem;
    text-transform: uppercase;
    font-weight: 800;
}
.nav-a-toggle-menu {
    text-decoration: none;
    color: $amulet-green-400;
    transition: 0.1s ease-in-out;
    &:hover {
        /* text-decoration: underline 0.2rem $eastern-blue-500; */
        transform: scale(1.05);
        color: $grey-600;
    }
}

//right side toggle menu
.toggle-column-right {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: auto;
    gap: 0.5rem;
}

#toggle-menu-a-1,
#toggle-menu-a-2,
#toggle-menu-a-3,
#toggle-menu-a-4,
#toggle-menu-a-5,
#toggle-menu-a-6,
#toggle-menu-a-7,
#toggle-menu-a-8,
#toggle-menu-a-9 {
    color: $white;
    font-size: 1.75rem;
    transition: 0.1s ease-in-out;

    &:hover {
        transform: scale(1.2);
        color: $grey-500;
    }
}

/* bottom navigation */
.bottom-navigation-only {
    height: 49.8%;
    width: 100vw;
}

.bottom-nav-ul {
    display: flex;
    justify-content: left;
    align-items: center;
    flex-direction: row;
    list-style: none;
    font-size: 0.9rem;
    padding: 0 1.5rem;
    width: 100vw;
    height: auto;
}

.bottom-li-nav {
    margin: 1rem;
    gap: 1.5rem;
}

#bottom-li-nav-about,
#bottom-li-nav-jobs,
#bottom-li-nav-appren {
    cursor: pointer;
}

#bottom-li-nav-about {
    color: $amulet-green-400;
}
.nav-a-social {
    display: none;
}
//section greetings

.section-greetings {
    display: flex;
    flex-direction: column;
    padding: 8rem 1rem 4rem 1rem;
    width: 100%;
}
.div-info-greetings {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    background-color: $shadow-black-transparent;
    padding: 2rem 1rem;
    opacity: 0.9;
    border-radius: 1rem 1rem 0 0;
}
#h5-section-greetings {
    font-size: 1.8rem;
    text-transform: uppercase;
    font-weight: 700;
}

.p-info {
    margin-top: 0.5rem;
    padding: 0;
    font-size: 1.2rem;
}

//btn
.btn-return-div {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    background-color: transparent;
    margin-top: 0.5rem;
}

.btn-return-link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    background-color: $grey-700;
    color: $white;
    width: 100%;
    height: auto;
    font-size: 1rem;
    font-weight: 700;
    transition: 0.1s ease-in-out;
    padding: 1rem 0.5rem;
    border-radius: 0 0 1rem 1rem;

    &:hover,
    :focus,
    :active {
        outline: none;
        background-color: $grey-600;
        color: $black-900;
    }
}

// footer

//top footer

.hr-line-footer {
    width: 100%;
    border: none;
    background-color: $grey-600;
    height: 0.05rem;
    opacity: 0.1;
}
footer {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
    height: auto;
    padding: 0rem 1rem;
}
.footer-top {
    display: flex;
    flex-direction: column; 
    align-items: flex-start;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    padding: 2rem 1rem;

}
.footer-a-1,
.footer-a-2 {
    display: flex;
    flex-direction: column;
    width: auto;
}

.nav-a-footer {
    color: $amulet-green-400;
    font-weight: 800;
    text-transform: uppercase;
    font-size: 1.4rem;
    text-decoration: none;
    transition: 0.1s ease-in-out;
    padding: 0.5rem; 
    &:hover {
        transform: scale(1.05);
        color: $grey-600;
    }
}
//footer form -newsletter
.description-and-email-input {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: auto;
    height: auto;
    gap: 0.5rem;
    width: 100%;
    height: auto;
}
.email-input-description {
    padding: 2rem 0 1rem 0;
    font-size: 1.2rem;
    font-weight: 400;
    color: $grey-600;

}
.input-wth-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: 100%;
    height: auto;
    border-bottom: 0.1rem solid $white;
}

.input-email {
    width: 80%;
    height: auto;
    line-height: 2rem;
    font-size: 1.4rem;
    background-color: transparent;
    color: $white;
    border: none;
    margin-right: 0;
    padding: 1rem 0;
    &:focus {
        outline: none;
  
    }
}
.send-btn {
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
   border-radius: 50%;
    border: 0.1rem solid ;
    background-color: transparent;
    color: $eastern-blue-500;
    padding: 0;
    margin-left: 0;
    width: auto;
    height: auto;
    transition: 0.2s ease-in-out;
    font-size: 1.4rem;
    padding: 0.5rem;
    z-index: 1;
    cursor: pointer;
    &:hover {
        transform: scale(1.1);
        color: $grey-500;
    }
    &:focus {
        transform: scale(1.1);
        color: $amulet-green-400;
    }
}

//bottom footer

.footer-bottom {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    height: auto;
}

.footer-p-and-nav {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    height: auto;
    gap: 1rem;
    padding-top: 1rem;
}

.nav-a-footer-social {
    padding: 0 0.5rem;
}

#bottom-footer-social-1,
#bottom-footer-social-2,
#bottom-footer-social-3,
#bottom-footer-social-4,
#bottom-footer-social-5,
#bottom-footer-social-6,
#bottom-footer-social-7,
#bottom-footer-social-8,
#bottom-footer-social-9 {
    font-size: 1.4rem;
    color: $grey-500;
    cursor: pointer;

    &:hover {
        transition: 0.1s ease-in-out;
        transform: scale(1.2);
        color: $amulet-green-400;
    }
}
.time-and-rights {
    font-size: 1rem;
    color: $grey-500;
    font-size: 600;
}
//about me
.about-me {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: auto;
    padding-top: 2rem;
}
#description-about-me {
    font-size: 1rem;
    width: 80%;
    padding-bottom: 1rem;
}
#anchor-description-about-me {
    text-decoration: none;
    color: $grey-500;
    font-weight: 800;
    transition: 0.1s ease-in-out;
    &:hover {
        color: $eastern-blue-500;
    }
}

#my-name-is {
    font-size: 1.5rem;
    padding: 0.5rem 0;
}
.my-social {
    font-size: 3rem;
    color: $ming-blue-600;
    transition: 0.1s ease-in-out;
    &:hover {
        color: $amulet-green-400;
    }
}
// animations
@keyframes loading {
    0% {
        transform: rotate(0deg);
    }
    50% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

//media queries for 375px screens

