//media queries for 425px screens

@media (min-width: 425px) {
    .top-content {
        padding: 0 2rem;
    }
    //toggle menu
    .all-inside {
        padding: 2rem;
    }
    .toggle_menu-a {
        font-size: 1.4rem;
    }

    //section greetings
    .section-greetings {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 8rem 2rem 4rem 2rem;
    }
    .div-info-greetings {
        padding: 3rem 2rem;
        width: 90%;
    }
    #h5-section-greetings {
        font-size: 2rem;
    }
    .p-info {
        margin-top: 0.5rem;
        font-size: 1.4rem;
    }
    .btn-return-div {
        height: 3.4rem;
        margin-top: 1rem;
        width: 90%;
    }
    .btn-return-link {
        font-size: 1.2rem;
    }

    //top footer
    footer {
        padding: 0rem 2rem;
        background-color: $black-900;
    }
    .nav-a-footer {
        font-size: 1.6rem;
    }
    //footer boottom

    #bottom-footer-social-1,
    #bottom-footer-social-2,
    #bottom-footer-social-3,
    #bottom-footer-social-4,
    #bottom-footer-social-5,
    #bottom-footer-social-6,
    #bottom-footer-social-7,
    #bottom-footer-social-8,
    #bottom-footer-social-9 {
        font-size: 1.8rem;
    }
    .time-and-rights {
        font-size: 1.2rem;
    }
    //about me
    #description-about-me {
        font-size: 1.2rem;
        width: 80%;
        padding-bottom: 1.5rem;
    }
}
// media q for 768px screens

@media (min-width: 768px) {
    //toggle menu
    .toggle-menu {
        width: 60%;
        right: 0;
    }

    //section 4
  
    .section-greetings {
        padding: 14rem 2rem 4rem 2rem;
    }
    .div-info-greetings {
        padding: 3rem 2rem;
        width: 80%;
    }
    #h5-section-greetings {
        font-size: 2.2rem;
    }
    .p-info {
        margin-top: 0.5rem;
        font-size: 1.4rem;
    }
    .btn-return-div {
        height: 3.4rem;
        margin-top: 1rem;
        width: 80%;
    }
    .btn-return-link {
        font-size: 1.2rem;
    }
}

//media q for 1024px

@media (min-width: 1024px) {
    // hide mobile menu and nav for desktop row - 1024+
    .top-content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: $shadow-black-transparent;
        backdrop-filter: blur(0.5rem);
        width: 100%;
        height: 3.5rem;
        padding: 0 2rem;
        position: fixed;
        z-index: 2;
    }

    #logo {
        display: block;
        width: 1.75rem;
        height: 1.75rem;
        font-size: 1.75rem;
        transition: ease-in-out 0.2s;
        cursor: pointer;
    }
    #chevron-to-show-social-menu {
        display: block;
        width: 1.75rem;
        height: 1.75rem;
        font-size: 1.75rem;
        transition: ease-in-out 0.2s;
        cursor: pointer;
        border-radius: 50%;
        &:hover {
            color: $grey-500;
            background-color: $black-600;
            border-radius: 50%;
        }
        &.rotate {
            transform: rotate(180deg);
          }
    }

    #dropdown-menu-icon {
        display: none;
    }

    .nav-desktop {
        display: block;
        width: 80%;
    }
    .desktop_menu-a {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: row;
        gap: 1.5rem;
        padding: 1rem 0rem;
        font-size: 1.1rem;
        text-transform: uppercase;
        font-weight: 800;
        margin: 0;
    }

    .nav-a-desktop-menu {
        text-decoration: none;
        color: $grey-500;
        transition: 0.1s ease-in-out;
