    .toggle-column-right {
        display: none;
    }

    .top-nav-desktop {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-direction: column;
        margin: 5rem 2rem;
        right: 0;
        gap: 0.5rem;
        position: fixed;
        z-index: 1;
    }
    .top-nav-desktop-hidden {
        display: none;
      }
    .nav-a-social {
        display: block; //test
        text-decoration: none;
        color: $white;
        margin: 0.2rem 0;
        cursor: pointer;
    }

    #nav-a-1,
    #nav-a-2,
    #nav-a-3,
    #nav-a-4,
    #nav-a-5,
    #nav-a-6,
    #nav-a-7,
    #nav-a-8,
    #nav-a-9 {
        font-size: 1.75rem;
        transition: 0.1s ease-in-out;
        &:hover {
            transform: scale(1.2);
            color: $grey-500;
        }
    }

    //sectio1
   
    //section greetings
    .section-greetings {
        padding: 14rem 4rem 4rem 4rem;
    }
    .div-info-greetings {
        padding: 4rem 3rem;
        width: 70%;
    }
    #h5-section-greetings {
        font-size: 2.2rem;
    }
    .p-info {
        margin-top: 0.5rem;
        font-size: 1.4rem;
    }
    .btn-return-div {
        height: 3.4rem;
        margin-top: 1rem;
        width: 70%;
    }
    .btn-return-link {
        font-size: 1.2rem;
    }
    //footer'top

    .footer-top {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
        gap: 4rem;
        padding: 2rem 4rem;
    }
    .footer-a-1,
    .footer-a-2 {
        gap: 1rem;
    }
    .description-and-email-input {
        width: 40%;
        gap: 1rem;
    }

    .email-input-description {
        padding: 0;
    }
    //footer bottom
    .footer-bottom {
        background-color: $black-900;
    }
    .footer-p-and-nav {
        padding-top: 2rem;
    }
    .nav-a-footer-social {
